@import "colors";

/* Fonts */
$base-font: "museo-sans", sans-serif;
$header-font: "museo-slab", serif;
$logo-font: "kinescope", script;

/* Font Sizes */
$font-size-base: 16px;
$font-size-xxl: $font-size-base * 3;
$font-size-xl: $font-size-base * 2.5;
$font-size-l: $font-size-base * 2;
$font-size-sm: $font-size-base * 0.875;
$font-size-xs: $font-size-base * 0.75;

$font-size-print-base: 10pt;
$font-size-print-xxl: $font-size-print-base * 3;
$font-size-print-xl: $font-size-print-base * 2.5;
$font-size-print-l: $font-size-print-base * 2;
$font-size-print-sm: $font-size-print-base * 0.875;
$font-size-print-xs: $font-size-print-base * 0.75;


/* Spacing */
$vertical-spacing: 15px;
$horizontal-spacing: 15px;

/* Grid */
$columns: 12 !default;

/* Breakpoint variables */
$xxsmall-screen-min: 320px !default;
$xsmall-screen-min: 480px !default;
$small-screen-min: 768px !default;
$medium-screen-min: 992px !default;
$large-screen-min: 1200px !default;

/* 479px */
/* 767px */
/* 991px */
/* 1199px */
$xxsmall-screen-max: $xsmall-screen-min - 1 !default;
$xsmall-screen-max: $small-screen-min - 1 !default;
$small-screen-max: $medium-screen-min - 1 !default;
$medium-screen-max: $large-screen-min - 1 !default;



